// SP
@keyframes menuAnimation {
  0% {
    width: 36px;
    height: 36px;
    transform: scale(1);
  }

  100% {
    transform: scale(80);
  }
}

// SP
@keyframes menuCloseAnimation {
  0% {
    width: 36px;
    height: 36px;
    transform: scale(80);
  }

  100% {
    transform: scale(1);
  }
}

// SP
@keyframes menuFadeInAnimation {
  0% {
    transform: translateY(10px);
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

// SP
@keyframes menuCloseFadeInAnimation {
  0% {
    transform: translateY(0);
    opacity: 1;
  }

  100% {
    transform: translateY(10px);
    opacity: 0;
  }
}

@keyframes historyExtendLineAnimation {
  0% {
    height: 0;
  }

  100% {
    height: calc(100% - 80px);
  }
}

@keyframes historyFadeInAnimation {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

// TopMV
@keyframes mvCarouselChangeAnimation {
  0% {
    transform: translate(-50%, -50%) scale(0);
  }

  100% {
    transform: translate(-50%, -50%) scale(10);
  }
}

// TopMV
@keyframes mvCarouselAnimation {
  0% {
    clip-path: circle(0.0% at 50% 50%);
    opacity: 1;
  }

  100% {
    clip-path: circle(100.0% at 50% 50%);
    opacity: 1;
  }
}

// Toppick up Services and Solution 
@keyframes pickUpAnimation {
  100% {
    transform: rotate(360deg);
  }
}

// js
@keyframes menuSlideLeftInAnimation {
  0% {
    transform: translateX(-10px);
    opacity: 0;
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes menuSlideRightInAnimation {
  0% {
    transform: translateX(10px);
    opacity: 0;
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

// 
@keyframes businessToggleAnimation {
  0% {
    display: block;
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes cardAnimation {
  0% {
    transform: translateY(10px);
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}